
import './styles.css';
import React, { useContext, useEffect } from 'react'

import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import {Menu} from './components/Pages/Menu';
import {Login} from './components/Pages/login';
import {MainPage} from './components/main';
import CRUD from './components/Admin/crud';
import {NotFound} from './components/Pages/404';
import {SignUp} from './components/Pages/sign-up.js';

//fetch data
import GetUsers from './components/Admin/getUsers';

//destructuring porque USerContext tiene 2 export, no tiene un export default  
import {UserContext} from './components/USerContext';

const App=()=>{
    const {value2}=useContext(UserContext);
    const [logged, setLogged]=value2;

    useEffect(()=>{

        let email=localStorage.getItem("auth-email"),
        password=localStorage.getItem("auth-password"),
        login_validate=localStorage.getItem("login-validate");

        if(email && password && login_validate){
            setLogged(prevState => !prevState)    ;
        }

    },[]);

    return(
        <Router>  
            <GetUsers />
            <Menu /> 
            <Switch>
                <Route exact path="/crud-app" component={MainPage}/>
                <Route exact path="/login" component={Login} />
                <Route exact path="/sign-up" component={SignUp} />
                <Route exact path="/crud" component={CRUD} />
                <Route component={NotFound} />
            </Switch>
        </Router>
    );

}

export default App;