import React, {useContext, useState} from 'react';
import {UserContext} from './USerContext';
import Modal from './modal.js';
import ReactPaginate from "react-paginate";

const User=()=>{

  const {value}=useContext(UserContext);
  const [users, setUsers]=value;
  const [handleModal, setHandleModal]=useState(false);
  const [user, SetUser]=useState([]);
  const [currentPage, setCurrentPage]=useState(0);
  const PER_PAGE=5; 

  
  const handlePageClick=({ selected: selectedPage }) =>{
      setCurrentPage(selectedPage);
    }

  const offset = currentPage * PER_PAGE;

  const printInModal=(userdata)=>{
        setHandleModal(prevState=> !prevState);
        SetUser(userdata);
    }

  const currentPageData=users
            .slice(offset, offset + PER_PAGE)
            .map(u=>{
                  return <div className="card" style={{width:'18rem'}} key={u.id}>
                              <div className="card-body">
                                <h5 className="card-title">{u.name}</h5>
                                <p className="card-text">{u.email}</p>
                                <button onClick={()=>printInModal(u)} className="btn btn-primary">Ver</button>
                              </div>
                        </div>
              });
  
  const pageCount = Math.ceil(users.length / PER_PAGE);

  return(
        <>
          {(handleModal) && <Modal userData={user} setHandleModal={setHandleModal}/>}
          <div className="grid-template">  
            
                {(users.length >0)
                    ? currentPageData
                    : <div><p>No hay informacion</p></div>}
          </div>
          <ReactPaginate
                previousLabel={"← Previous"}
                nextLabel={"Next →"}
                pageCount={pageCount}
                onPageChange={handlePageClick}
                containerClassName={"pagination"}
                previousLinkClassName={"pagination__link"}
                nextLinkClassName={"pagination__link"}
                disabledClassName={"pagination__link--disabled"}
                activeClassName={"pagination__link--active"}
            />
        </>
    );
}

export default User;