import React, {useEffect, useContext} from 'react';
import {UserContext} from '../USerContext';
import {store} from '../../store.js';

const GetUsers=()=>{

    const {value} = useContext(UserContext);
    const [users, setUsers]=value;

    useEffect(()=>{
        setUsers(store);
    },[]);

    return(null);
}

export default GetUsers;