import React from 'react';


const Modal=({userData, setHandleModal})=>{

    return(
        <div className="modal" tabIndex="-1" role="dialog" style={{
            display:'block',
            marginTop:'5em'
        }}>
            <div className="modal-dialog" role="document">
                <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title">About</h5>
                            <button onClick={()=>{
                                setHandleModal(prevState=>!prevState)
                            }} type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                                <p>ID: {userData.id}</p>
                                <p>Name : {userData.name}</p>
                                <p>Username: {userData.username}</p>
                                <p>Email : {userData.email}</p>
                                <p>Address: {userData.address.street}</p>
                                
                                
                        </div>
                        <div className="modal-footer">
                            <button onClick={()=>{
                                setHandleModal(prevState=>!prevState)
                            }} type="button" className="btn btn-primary" data-dismiss="modal">Close</button>
                        </div>
                </div>
            </div>
        </div>
    );
}

export default Modal;