import React, {useState} from 'react';
import { useHistory } from "react-router-dom";


export const SignUp=()=>{
    const [user, setUser]=useState("");
    const [pass, setPass]=useState("");
    const history=useHistory();

    const signup=e=>{
        e.preventDefault();
        localStorage.setItem("auth-email", `${user}`);
        localStorage.setItem("auth-password", `${pass}`);
        localStorage.setItem("login-validate", "");

        history.push("/login");
    }

    return(
        <>
            <div id="login-card" className="card">
                <div className="card-body">
                    <h2 className="text-center">SIGN UP</h2>
                        <br/><br/>
                        <form className="form" onSubmit={signup}>
                            <div className="form-group">
                                <label htmlFor="exampleInputEmail1">Email address</label>
                                <input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" value={user} onChange={e=>setUser(e.target.value)}/>
                                <small id="emailHelp" className="form-text text-muted">Tu informacion es privada y no sera compartida con nadie.</small>
                            </div>
                            <div className="form-group">
                                <label htmlFor="exampleInputPassword1">Password</label>
                                <input type="password" className="form-control" id="exampleInputPassword1" placeholder="Password" required minLength="3" maxLength="20" value={pass} onChange={e=> setPass(e.target.value)}/>
                            </div>
                            <input type="submit" value="submit" className="btn btn-primary"/>
                            </form>
                </div>
            </div>    
        </>
    );
}