import React, {useContext} from 'react';
import {NavLink} from 'react-router-dom';
import { useHistory } from "react-router-dom";
import {UserContext } from '../USerContext';


export const Menu=()=>{

    const {value2} = useContext(UserContext);
    const [logged, setLogged] = value2;
    const history=useHistory();
  
    return(
        <header>
        <nav className="nav">
            <ul>
                <li className="nav-item active"><NavLink exact to="/crud-app">Home</NavLink></li>
                {!logged && <li className="nav-item"><NavLink exact to="/login">Sign In</NavLink></li>}
                {!logged && <li className="nav-item"><NavLink exact to="/sign-up">Sign Up</NavLink></li>}
                {logged && <li className="nav-item"><NavLink exact to="/crud">CRUD</NavLink></li>}
                {logged && <li className="nav-item"><button  onClick={()=>{
                    setLogged(prev=> !prev)
                        localStorage.setItem("auth-email","");
                        localStorage.setItem("auth-password","");

                        history.push("/crud-app");
                    }} className="btn btn-danger">LogOut</button></li>}
            </ul>
        </nav>
    </header>
    );
    
}
