import React, {useContext, useState} from 'react';
import {Redirect} from 'react-router-dom';
import {UserContext} from '../USerContext';



export const Login=()=>{
    const {value2}= useContext(UserContext);
    const [logged, setLogged]=value2;
    const [user, setUser]=useState("");
    const [pass, setPass]=useState("");

    const signin=e=>{
        e.preventDefault();
        let usr=localStorage.getItem("auth-email");
        let password=localStorage.getItem("auth-password");
        
        if(usr === user && password === pass){
            setLogged(state => !state);        
            localStorage.setItem("login-validate", "true");
            
        }else{
            alert("usuario o contraseña incorrecta");
            setUser("");
            setPass("");
        }
        
    }

    return(
        <>
        <div id="login-card" className="card">
            <div className="card-body">
                <h2 className="text-center">SIGN IN</h2>
                    <br/><br/>
                    <form  onSubmit={signin}>
                    <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Email address</label>
                        <input type="email" id="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" value={user} onChange={e=>setUser(e.target.value)}/>
                        <small id="emailHelp" className="form-text text-muted">Tu informacion es privada y no sera compartida con nadie.</small>
                    </div>
                    <div className="form-group">
                        <label htmlFor="exampleInputPassword1">Password</label>
                        <input type="password" id="email" className="form-control" id="exampleInputPassword1" placeholder="Password" required minLength="3" maxLength="20" value={pass} onChange={e=> setPass(e.target.value)}/>
                    </div>
                    <input type="submit" id="button" value="submit" className="btn btn-primary deep-purple btn-block"/>
                    </form>
                </div>
            </div>
            {
                (logged) && <Redirect to="/crud" /> 
            }
    </>
    );
}