import React, {useContext} from 'react';
import {UserContext} from '../USerContext';
import User from './User';
import {NotFound} from '../Pages/404';

 const CRUD=()=>{

     const {value2}=useContext(UserContext);
     const [logged, setLogged]=value2;

    return(
        <>
        {
            logged ?
                (<User />)
                :
            (<NotFound />)
        }
        </>
    );
}

export default CRUD;