import React, {useContext, useState} from 'react';
import {UserContext} from '../USerContext';


const User=()=>{
    const {value}= useContext(UserContext);
    const [users, setUsers]=value;
    //post user
    const [name, setName]=useState('');
    const [username, setUser]=useState('');
    const [email, setEmail]=useState('');
    const [address, setAddress]=useState('');


  //POST DATA
    const submit=e=>{
      e.preventDefault();

      //crear objeto y agregar propiedades con valores que vienen del form
      const User={};
      User.id=users.length+1;
      User.name=name;
      User.username=username;
      User.email=email;
      User.address={street:address};
      
      console.log(User);
      //Actualizar estado [estado anterior, nuevo objeto]
      setUsers([...users, User]);
      //limpiar inputs
      setName('');
      setUser('');
      setEmail('');
      setAddress('');
    }

    //DELETE DATA
    const deleteUser=id=>{

      const Users=users.filter(us =>{
        return us.id !== id
      });

      setUsers([...Users]);
    }

    //UPDATE DATA
    //recibimos por parametros el id del campo en BD, nombre del input y valor del input
    const setUpdate=(id, nameInput, value)=>{
      let Users=users;

      //checa que coincidan campos de la BD con los name del input
      Users.map(user=>{
          if(user.id === id){
              if(nameInput === "name") 
                    user.name = value 

              if(nameInput === "username")
                    user.username = value 

              if(nameInput === "email")
                    user.email = value 

              if(nameInput === "address")
                    user.address.street = value

          }
      });

      //actualizo
      setUsers([...Users]);
    }
    
    
    return(
        <>
          <div className="card">
              <div className="card-body">
          
                  <form onSubmit={submit} className="form-create">
                  <h2 className="text-center">Create a new user</h2>
                    <input type="text" placeholder="nombre" value={name} onChange={e=> setName(e.target.value)} />
                    <input type="text" placeholder="usuario" value={username} onChange={e=>setUser(e.target.value)}/>
                    <input type="email" placeholder="correo" value={email} onChange={e=> setEmail(e.target.value)}/>
                    <input type="text" placeholder="direccion"  value={address} onChange={e=> setAddress(e.target.value)}/>
                    <input className="btn btn-success" type="submit" value="Create"/>
                  </form>
              </div>
          </div>
          <table>
                <thead>
                  <tr>
                    <th scope="col">Nombre</th>
                    <th scope="col">Usuario</th>
                    <th scope="col">Correo E</th>
                    <th scope="col">Direccion</th>
                    <th scope="col">Opcion</th>
                  </tr>
                </thead>
                <tbody>
                  {
                    users.map(u=>{
                      return( 
                          <tr key={u.id}>
                            <td><input type="text" name="name" value={u.name} onChange={ e=> setUpdate(u.id, e.target.name,e.target.value) } /></td>
                            <td><input type="text" name="username" value={u.username} onChange={ e=> setUpdate(u.id, e.target.name,e.target.value) } /> </td>
                            <td><input type="email" name="email" value={u.email} onChange={ e=> setUpdate( u.id, e.target.name,e.target.value) } /> </td>
                            <td><input type="text" name="address" value={u.address.street} onChange={ e=> setUpdate( u.id, e.target.name,e.target.value) } /> </td>
                            <td><button className="btn btn-danger" onClick={e=>deleteUser(u.id)}>Eliminar</button></td>
                          </tr>)
                  })
                  }
                </tbody>
            </table>            
        </>
    );
}

export default User;